import 'bootstrap/dist/css/bootstrap.min.css';
import Link from 'next/link'

import React from "react";
import {
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    NavLink,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
    NavbarText
} from 'reactstrap';
import {AuthorizedView, NotAuthorizedView, useGigyaAuth} from "../gigya";

const LinkButton = props => <button
    {...props}
    type="button"
    className="link-button"
>
    {props.children}
</button>


const Logout = props => {
    const {logout} = useGigyaAuth();
    return <LinkButton {...props} onClick={logout}>Logout</LinkButton>;
}


export function Bar({openScreen}) {
    const {account, logout} = useGigyaAuth();
    return (
        <Navbar color="light" light expand="md">
            <NavbarBrand href="/"> Id-Booom
            </NavbarBrand>

            <Nav className="mr-auto" navbar>
                <NavItem>
                    <NavLink href="/#/">#</NavLink>
                </NavItem>

                <NavItem>
                    <NotAuthorizedView>
                        <Link  href={"/gigya/lite"}>Subscribe</Link>
                    </NotAuthorizedView>
                </NavItem>
                
                <NavItem>
                    <NotAuthorizedView>
                        <Link href="../gigya/pages/login">Login</Link>
                    </NotAuthorizedView>
                </NavItem>

                <NavItem>
                    <AuthorizedView> 
                        <Link href="auth/profile/"> {account && account.profile.email} </Link>
                    </AuthorizedView>
                </NavItem>

                <NavItem> 
                    <AuthorizedView> 
                        <Link href="auth/logout/" onClick={logout}>Logout</Link> 
                    </AuthorizedView>
                </NavItem>

            </Nav>
        </Navbar>
    )
        ;
}
